@font-face {
  font-family: 'Cantarell';
  src: url(./assets/Cantarell-Bold.ttf);
}

@font-face {
  font-family: 'Roboto';
  src: url(./assets/RobotoCondensed-Regular.ttf);
}

:root {
  --lightGrey: #e0dfdf;
  --darkBlue: #534b52;
  --red: #e75a7c;
  --grey : #7e8287;
}

*, html {
  margin: 0;
  padding: 0;
  font-family: 'Roboto';
  background-color: var(--lightGrey);
  color: var(--darkBlue);
  transition: 0.07s ease-in-out 0s;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
  background: var(--red);
  color: var(--lightGrey);
}

ul {
  list-style: none;
}