.App {
  margin-right: auto;
  margin-left: auto;
  width: 70%;
  font-size: 16px;
}

.box {
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: -6px -6px 26px 0px rgba(255, 255, 255, 0.8), 6px 6px 16px 5px rgba(197, 191, 183, 0.9);
  margin: 1.3rem 0 1.3rem 0;
  padding: 1rem;
  font-size: 1.3rem;
  border-radius: 20px;
}

.box-inverted {
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: -6px -6px 26px 0px rgba(255, 255, 255, 0.8) inset, 6px 6px 16px 5px rgba(197, 191, 183, 0.9) inset;
  margin: 1.3rem 0 1.3rem 0;
  padding: 1rem;
  font-size: 1.3rem;
  border-radius: 20px;
}

.box-no-shadow {
  margin: 1rem;
  padding: 0.2rem;
}
/* 
.barcode {
  font-family: 'Barcode';
}
*/
button {
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: -6px -6px 26px 0 rgba(255, 255, 255, 0.9), 6px 6px 16px 0 rgba(197, 191, 183, 0.5);
  font-size: 1rem;
  border-radius: 6px;
  padding: 0.2rem;
  margin: 0.4rem 0 0.2rem 0;
  color: var(--red);
}

button::-moz-focus-inner {
  border: 0;
}

input {
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: -6px -6px 26px 0px rgba(255, 255, 255, 0.8) inset, 6px 6px 16px 5px rgba(197, 191, 183, 0.9) inset;
  width: 100%;
  box-sizing: border-box;
  padding: 0.5rem;
  font-size: 1.3rem;
  border-radius: 20px;
  margin: 0.5rem 0 0.5rem 0;
  caret-color: var(--red);
  color: var(--darkBlue);
}

@media only screen and (max-width: 600px) {
  .App {
    width: 100%;
  }
}

@media only screen and (min-width: 968px) {
  .App {
    width: 968px;
  }
}