@font-face {
  font-family: 'Barcode';
  src: url(../assets/LibreBarcode39-Regular.ttf);
}

.link-item {
  display: flex;
  flex-direction: column;
}

.link-title {
  margin-bottom: 0.2rem;
  color: var(--red);
}

.link-description {
  font-size: 1rem;
  margin-bottom: 0.5rem;
}

.link-barcode {
  font-family: 'Barcode';
  font-size: 1.3rem;
  align-self: flex-end;
  margin-right: 0.2rem;
}

@media only screen and (min-width: 600px) {
  .link-item:hover {
    background-color: var(--red);
    color: var(--lightGrey);
    cursor: pointer;
  }
  
  .link-item:hover * {
    background-color: var(--red);
    color: var(--lightGrey);
    cursor: pointer;
  }
}