.todo-item {
  display: flex;
  justify-content: space-between;
}

.todo-button {
  flex: 1;
  margin: 0.2rem 0.2rem;
  width: 1rem;
  text-align: center;
}

.todo-title {
  align-self: start;
}

.todo-buttons-list {
  align-self: end;
  font-size: 1.3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media only screen and (min-width: 600px) {
  .todo-item:hover {
    box-shadow: none;
  }
  
  .todo-item:hover .todo-title {
    color: var(--red);
  }
  
  .todo-button:hover {
    color: var(--red);
    cursor: pointer;
  }
}