.add-article-form label {
  color: var(--red);
}

.add-article-buttons {
  display: flex;
  justify-content: flex-end;
}

.add-article-instructions {
  margin-bottom: 1rem;
}

.add-article-button:hover {
  box-shadow: none;
  cursor: pointer;
}

.add-article-button:active {
  box-shadow: 3px 3px 8px 0 rgba(217, 210, 200, 0.5) inset;
}