.article-item {
  display: flex;
  justify-content: space-between;
}

.article-button {
  flex: 1;
  margin: 0.2rem 0.2rem;
  width: 1rem;
  text-align: center;
}

.article-title {
  align-self: start;
}

.article-buttons-list {
  align-self: end;
  font-size: 1.3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.article-item:hover {
  box-shadow: none;
}

.article-item:hover .article-title {
  color: var(--red);
}

.article-button:hover {
  color: var(--red);
  cursor: pointer;
}