.add-todo-form {
  position: relative;
  align-content: center;
}

.add-todo-form label {
  color: var(--red);
}

.add-todo-buttons {
  display: flex;
  justify-content: flex-end;
}

.add-todo-button:hover {
  box-shadow: none;
  cursor: pointer;
}

.add-todo-button:active {
  box-shadow: 3px 3px 8px 0 rgba(217, 210, 200, 0.5) inset;
}